@import url("https://fonts.googleapis.com/css2?family=DM+Sans:ital,opsz,wght@0,9..40,100..1000;1,9..40,100..1000&display=swap");
.cbx-chota {
  --color-primary: #6648fe;
  --color-secondary: #747681;
}

.cbx-chota {
  --bg-color: #ffffff;
  --bg-secondary-color:#f3f3f6;
  --color-lightGrey: #d2d6dd;
  --color-grey: #747681;
  --color-darkGrey: #3f4144;
  --color-error: #d43939;
  --color-success: #339D5D;
  --color-white: #ffffff;
  --grid-maxWidth: 120rem;
  --grid-gutter: 2rem;
  --font-size: 0.875rem;
  --font-color: #333333;
  --font-family-sans: "DM Sans", -apple-system, BlinkMacSystemFont, Avenir, "Avenir Next",
  "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
  "Droid Sans", "Helvetica Neue", sans-serif;
  --font-family-mono: monaco, "Consolas", "Lucida Console", monospace;
  /*p{
    font-size: var(--font-size);
    line-height: 1.15;
  }*/
}

.cbx-chota .wpsa-browse-wrap, .cbx-chota .permalink-wrap {
  display: flex;
  flex-wrap: wrap;
}
.cbx-chota .wpsa-browse-wrap .wpsa-url, .cbx-chota .wpsa-browse-wrap .permalink-slug, .cbx-chota .permalink-wrap .wpsa-url, .cbx-chota .permalink-wrap .permalink-slug {
  flex: 1;
  border-radius: 4px 0 0 4px !important;
  border-right: 0 none !important;
  box-shadow: none !important;
  margin-right: 0 !important;
}
.cbx-chota .wpsa-browse-wrap .wpsa-url:focus, .cbx-chota .wpsa-browse-wrap .permalink-slug:focus, .cbx-chota .permalink-wrap .wpsa-url:focus, .cbx-chota .permalink-wrap .permalink-slug:focus {
  border-color: var(--color-primary) !important;
}
.cbx-chota .wpsa-browse-wrap .wpsa-browse, .cbx-chota .wpsa-browse-wrap .clear-permalink, .cbx-chota .permalink-wrap .wpsa-browse, .cbx-chota .permalink-wrap .clear-permalink {
  margin-left: 0 !important;
  border-radius: 0 4px 4px 0 !important;
}

.cbx-page-wrapper {
  margin-top: 50px;
}
.cbx-page-wrapper .container, .cbx-page-wrapper #poststuff {
  max-width: 1200px;
  margin: 0 auto;
}

.global_setting_heading_section h3.setting_heading {
  cursor: pointer;
  color: var(--color-primary);
  font-size: 18px !important;
  font-weight: normal !important;
  line-height: 1.5 !important;
  background-color: rgba(93, 93, 255, 0.13);
  padding: 5px 8px !important;
  margin-bottom: 8px !important;
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 15px;
}
.global_setting_heading_section h3 a.setting_heading_toggle {
  background-repeat: no-repeat;
  background-size: contain;
  text-align: center;
}
.global_setting_heading_section h3 a.setting_heading_toggle .setting_heading_toggle_plus {
  display: none;
}
.global_setting_heading_section h3 a.setting_heading_toggle .setting_heading_toggle_minus {
  display: block;
}
.global_setting_heading_section h4.setting_subheading {
  color: var(--color-primary);
  font-size: 22px;
  font-weight: 700;
  background-color: rgba(0, 90, 224, 0.06);
  padding: 15px 20px;
  border-radius: 5px;
}
.global_setting_heading_section .description {
  padding-left: 20px;
}

.global_setting_heading_section_closed h3.setting_heading {
  margin: 0 !important;
}
.global_setting_heading_section_closed h3.setting_heading a.setting_heading_toggle {
  background-repeat: no-repeat;
  background-size: contain;
}
.global_setting_heading_section_closed h3.setting_heading a.setting_heading_toggle .setting_heading_toggle_plus {
  display: block;
}
.global_setting_heading_section_closed h3.setting_heading a.setting_heading_toggle .setting_heading_toggle_minus {
  display: none;
}
.global_setting_heading_section_closed .description {
  display: none;
}

#global_setting_group_actions {
  display: none;
  margin-bottom: 0;
}
@media screen and (max-width: 782px) {
  #global_setting_group_actions {
    margin-top: 10px;
    text-align: center;
  }
  #global_setting_group_actions .pull-right {
    float: none !important;
  }
}

.magic_radio_fields .magic-radio-field {
  margin-bottom: 10px;
}
.magic_radio_fields.radio_fields_inline .magic-radio-field {
  display: inline-block;
  margin-right: 10px;
}
.magic_radio_fields.radio_fields_inline .magic-radio-field:last-child {
  margin-right: 0;
}

.checkbox_field.magic_checkbox_field {
  margin-bottom: 10px;
}

.checkbox_fields_check_actions {
  margin-bottom: 20px !important;
}

.magic_checkbox_fields.checkbox_fields_sortable .checkbox_field_placeholder {
  outline: 1px dashed #000;
  height: 30px;
}
.magic_checkbox_fields.checkbox_fields_sortable .checkbox_field {
  padding-left: 40px;
}
.magic_checkbox_fields.checkbox_fields_sortable .checkbox_field .checkbox_field_handle {
  width: 20px;
  height: 20px;
  margin-right: 20px;
  margin-left: -30px;
  cursor: move;
  background-image: url("../icons/icon_move.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  display: inline-block;
  float: left;
}
.magic_checkbox_fields.checkbox_fields_inline .checkbox_field {
  display: inline-block;
  margin-right: 10px;
}
.magic_checkbox_fields.checkbox_fields_inline .checkbox_field:last-child {
  margin-right: 0;
}

.xyz-filter-wrap {
  display: flex;
}
.xyz-filter-wrap .xyz-filter-selected-wrap {
  display: flex;
}
.xyz-filter-wrap .xyz-filter-selected-wrap .xyz-filter-selected {
  border-right: 0 !important;
  border-radius: 4px 0 0 4px !important;
}
.xyz-filter-wrap .xyz-filter-selected-wrap .xyz-filter-selected-remove {
  border-radius: 0 4px 4px 0 !important;
  margin-left: 0 !important;
  cursor: pointer;
}

.setting_pages_actions_wrapper {
  display: flex;
  flex-wrap: wrap;
}
.setting_pages_actions_wrapper .selecttwo-select-wrapper {
  width: 100%;
  max-width: 500px;
  flex: 1; /* take the remaining space */
}
.setting_pages_actions_wrapper .setting_pages_actions {
  padding-left: 10px;
  flex: 0 0 auto; /* only take as much space as content needs */
}

.description_note_on {
  color: var(--color-success);
}

.description_note_off {
  color: var(--color-error);
}

.cbx-chota .setting-form-wrap {
  display: flex;
}
@media screen and (max-width: 782px) {
  .cbx-chota .setting-form-wrap {
    display: block;
  }
}
.cbx-chota .setting-form-wrap .setting-tabs-nav {
  display: block;
  width: 250px;
  margin-right: 10px;
  border-right: 1px solid var(--color-lightGrey);
}
.cbx-chota .setting-form-wrap .setting-tabs-nav a {
  display: block;
  text-align: left;
  border-radius: 0;
  padding: 8px 15px;
  border-bottom: 0 none;
  margin-bottom: 5px;
  border-left: 2px solid #fff;
}
.cbx-chota .setting-form-wrap .setting-tabs-nav a.active {
  margin-bottom: 0;
  background-color: rgba(93, 93, 255, 0.13);
  color: var(--color-primary);
  border-bottom: 0 none;
  border-left: 2px solid var(--color-primary);
}
.cbx-chota .setting-form-wrap .setting-tabs-nav a:hover {
  background-color: transparent;
  color: var(--color-primary);
  border-bottom: 0 none;
  border-left: 2px solid var(--color-grey);
}
.cbx-chota .setting-form-wrap #setting-tabs-contents {
  flex: 2;
}
.cbx-chota .setting-form-wrap .setting-select-wrapper {
  width: 100% !important;
  display: none;
}
.cbx-chota .setting-form-wrap .setting-select-wrapper select {
  width: 100% !important;
}
.cbx-chota .setting-form-wrap .setting-select-wrapper .select2-container--cbx {
  width: 100% !important;
  max-width: 100% !important;
}
@media screen and (max-width: 782px) {
  .cbx-chota .setting-form-wrap .setting-tabs {
    display: none !important;
  }
  .cbx-chota .setting-form-wrap .setting-select-wrapper {
    display: block !important;
  }
  .cbx-chota .setting-form-wrap .setting-select-wrapper .select2-container--cbx {
    margin-top: 20px;
  }
}
.cbx-chota .cbx-export-import-wrapper {
  display: none;
}
.cbx-chota .cbx-export-import-wrapper .cbx-export-import-inner {
  background: #fff;
  padding: 20px;
  margin-bottom: 20px;
  border-radius: 7px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.cbx-chota .cbx-export-import-wrapper .cbx-export-import-inner h2 {
  font-size: 18px !important;
  margin: 0;
}

.global_setting_submit_buttons_wrap {
  display: flex;
  flex-wrap: wrap;
  margin: 20px 0 0 0 !important;
}
.global_setting_submit_buttons_wrap .pull-right {
  margin-left: auto !important;
}
.global_setting_submit_buttons_wrap .pull-left {
  margin-right: auto !important;
}
.global_setting_submit_buttons_wrap p.submit {
  margin-left: auto !important;
  padding: 0 !important;
  margin-bottom: 0 !important;
}
.global_setting_submit_buttons_wrap details summary {
  cursor: pointer;
  margin-left: 5px;
  border: 1px solid var(--color-primary);
  border-radius: 50%;
  /*&:before{
    height: 28px;
    width: 28px;
    font-size: 28px;
    line-height: 1;
  }*/
}

.cbxchota-setting-common #setting-error-settings_updated {
  display: flex !important;
  padding-left: 5px !important;
  padding-right: 5px !important;
  align-items: center !important;
}
.cbxchota-setting-common #setting-error-settings_updated p {
  line-height: 30px;
  margin-bottom: 0 !important;
}
.cbxchota-setting-common #setting-error-settings_updated button {
  margin-left: auto !important;
  margin-bottom: 0 !important;
  position: relative !important;
  border: 1px solid var(--color-darkGrey) !important;
  background-color: transparent !important;
  padding: 0 4px !important;
}
.cbxchota-setting-common #setting-error-settings_updated button:before {
  width: 20px;
  height: 20px;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url("../icons/icon_close.svg") !important;
  content: "";
}
.cbxchota-setting-common .postbox {
  border-radius: 8px;
  box-shadow: 0 10px 22px 0 rgba(0, 4, 32, 0.05) !important;
  border: 0 none;
}

.cbxchota-setting_input_file_wrap {
  display: flex;
  width: 100% !important;
  max-width: 500px;
}
.cbxchota-setting_input_file_wrap .cbxchota-setting_marker_preview {
  margin-left: 5px;
  width: 30px;
  height: 30px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  border: 2px dotted #333;
  margin-right: 10px;
  border-radius: 5px;
}
.cbxchota-setting_input_file_wrap .cbxchota-setting_filepicker_btn:hover {
  background-color: var(--color-primary);
  color: #fff;
}
.cbxchota-setting_input_file_wrap .cbxchota-setting_filepicker_btn.cbxchota-setting_filepicked {
  border-top-right-radius: 0 !important;
  border-end-end-radius: 0 !important;
}
.cbxchota-setting_input_file_wrap .cbxchota-setting_filepicker_btn.cbxchota-setting_left_space {
  margin-left: 5px;
}
.cbxchota-setting_input_file_wrap .cbxchota-setting_marker {
  position: relative;
  width: 30px;
  height: 30px;
  margin-left: 5px;
  margin-right: 5px;
  border-radius: 5px;
  background-size: cover;
  background-position: center;
  background-color: transparent;
}
.cbxchota-setting_input_file_wrap .cbxchota-setting_marker_hide,
.cbxchota-setting_input_file_wrap .cbxchota-setting_trash_hide {
  display: none !important;
}
.cbxchota-setting_input_file_wrap .cbxchota-setting_trash {
  font-size: 30px;
  cursor: pointer;
  color: #FFFFFF;
  background: rgba(255, 0, 0, 0.4705882353);
  border: 1px solid rgba(255, 0, 0, 0.4705882353);
  height: 30px;
  width: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top-right-radius: 3px;
  border-end-end-radius: 3px;
  box-sizing: border-box;
  border-left: none;
}
.cbxchota-setting_input_file_wrap .cbxchota-setting_trash:hover {
  color: rgba(255, 0, 0, 0.4705882353);
  background: #FFFFFF;
}

.description {
  display: block;
  margin-top: 5px;
}
.description.description_thumbsize_by_post_type {
  margin-bottom: 10px;
  font-weight: bold;
}

.thumbnail_post_types .thumbnail_post_type {
  display: flex;
  margin-bottom: 5px;
}
.thumbnail_post_types .thumbnail_post_type span {
  min-width: 100px;
  padding-right: 10px;
}
.thumbnail_post_types .thumbnail_post_type select {
  max-width: 300px;
}