.cbx-page-wrapper{
  margin-top: 50px;

  .container, #poststuff {
    max-width: 1200px;
    margin: 0 auto;
  }

}

.global_setting_heading_section {
  h3 {
    &.setting_heading {
      cursor: pointer;
      color: var(--color-primary);
      font-size: 18px !important;
      font-weight: normal  !important;
      line-height: 1.5 !important;
      background-color: rgba(93,93,255,.13);
      padding: 5px 8px !important;
      margin-bottom: 8px !important;
      border-radius: 5px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 15px;
    }

    a {
      &.setting_heading_toggle {
        background-repeat: no-repeat;
        background-size: contain;
        //width: 30px;
        text-align: center;
        .setting_heading_toggle_plus{
          display: none;
        }

        .setting_heading_toggle_minus{
          display: block;
        }
      }
    }
  }


  h4 {
    &.setting_subheading {
      color: var(--color-primary);
      font-size: 22px;
      font-weight: 700;
      background-color: rgba(0, 90, 224, .06);
      padding: 15px 20px;
      border-radius: 5px;
    }
  }

  .description {
    padding-left: 20px;
  }
}

.global_setting_heading_section_closed {
  h3 {
    &.setting_heading {
      margin: 0 !important;

      a {
        &.setting_heading_toggle {
          background-repeat: no-repeat;
          background-size: contain;

          .setting_heading_toggle_plus{
            display: block;
          }

          .setting_heading_toggle_minus{
            display: none;
          }
        }
      }

    }
  }

  .description {
    display: none;
  }
}

#global_setting_group_actions {
  display: none;
  margin-bottom: 0;

  @media screen and (max-width: 782px) {
    margin-top: 10px;
    text-align: center;
    .pull-right {
      float: none !important;
    }
  }
}



.magic_radio_fields {
  .magic-radio-field{
    margin-bottom: 10px;
  }
  &.radio_fields_inline{
    .magic-radio-field{
      display: inline-block;
      margin-right: 10px;

      &:last-child{
        margin-right: 0;
      }
    }
  }
}

.checkbox_field{
  &.magic_checkbox_field{
    margin-bottom: 10px;
  }
}



.checkbox_fields_check_actions{
  margin-bottom: 20px !important;
}

.magic_checkbox_fields{
  &.checkbox_fields_sortable{
    .checkbox_field_placeholder {
      outline: 1px dashed #000;
      height: 30px;
    }
    .checkbox_field{
      padding-left: 40px;
      .checkbox_field_handle{
        width: 20px;
        height: 20px;
        margin-right: 20px;
        margin-left: -30px;
        cursor: move;
        background-image: url("../icons/icon_move.svg");
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
        display: inline-block;
        float: left;
      }
    }
  }
  &.checkbox_fields_inline{
    .checkbox_field{
      display: inline-block;
      margin-right: 10px;
      &:last-child{
        margin-right: 0;
      }
    }
  }
}


.xyz-filter-wrap {
  display: flex;

  .xyz-filter-selected-wrap {
    display: flex;

    .xyz-filter-selected {
      border-right: 0 !important;
      border-radius: 4px 0 0 4px !important;

    }

    .xyz-filter-selected-remove {
      border-radius: 0 4px 4px 0 !important;
      margin-left: 0 !important;
      cursor: pointer;
    }
  }
}

.setting_pages_actions_wrapper{
  display: flex;
  flex-wrap: wrap;

  .selecttwo-select-wrapper{
    width: 100%;
    max-width: 500px;
    flex: 1; /* take the remaining space */
  }

  .setting_pages_actions{
    padding-left: 10px;
    //flex: 2;
    flex: 0 0 auto; /* only take as much space as content needs */
  }
}

.description_note_on{
  color : var(--color-success);
}
.description_note_off{
  color : var(--color-error);
}

.cbx-chota{
  .setting-form-wrap {
    display: flex;

    @media screen and (max-width: 782px) {
      display: block;
    }

    .setting-tabs-nav{
      //margin-top: 20px;
      display: block;
      width: 250px;
      margin-right: 10px;

      border-right: 1px solid var(--color-lightGrey);

      a{
        display: block;
        text-align: left;
        border-radius: 0;
        padding: 8px 15px;
        border-bottom: 0 none;
        margin-bottom:5px;
        border-left: 2px solid #fff;

        &.active{
          margin-bottom: 0;
          background-color: rgba(93,93,255,.13);
          color: var(--color-primary);
          border-bottom: 0 none;
          border-left: 2px solid var(--color-primary);
        }
        &:hover{
          background-color: transparent;
          color: var(--color-primary);
          border-bottom: 0 none;
          border-left: 2px solid var(--color-grey);
        }
      }

    }

    #setting-tabs-contents{
      flex: 2;
    }

    .setting-select-wrapper {
      width: 100% !important;
      display: none;

      select {
        width: 100% !important;
      }

      .select2-container--cbx {
        width: 100% !important;
        max-width: 100% !important;
      }
    }

    @media screen and (max-width: 782px) {
      .setting-tabs {
        display: none !important;
      }
      .setting-select-wrapper {

        display: block !important;
        .select2-container--cbx{
          margin-top: 20px;
        }
      }
    }
  }

  .cbx-export-import-wrapper {
    display: none;

    .cbx-export-import-inner {
      background: #fff;
      padding: 20px;
      margin-bottom: 20px;
      border-radius: 7px;
      display: flex;
      justify-content: space-between;
      align-items: center;

      h2 {
        font-size: 18px !important;
        margin: 0;
      }
    }
  }
}

.global_setting_submit_buttons_wrap{
  display: flex;
  flex-wrap: wrap;
  margin: 20px 0 0 0 !important;

  .pull-right{
    margin-left: auto !important;
  }

  .pull-left{
    margin-right: auto !important;
  }

  p.submit{
    margin-left: auto !important;
    padding: 0 !important;
    margin-bottom: 0 !important;
    //margin: 0 10px 0 0 !important;
  }

  details{
    summary{
      cursor: pointer;
      margin-left: 5px;
      border: 1px solid var(--color-primary);
      border-radius: 50%;
      //height: 28px;
      //width: 28px;
      /*&:before{
        height: 28px;
        width: 28px;
        font-size: 28px;
        line-height: 1;
      }*/
    }
  }
}


.cbxchota-setting-common{
  #setting-error-settings_updated{
    display: flex !important;
    padding-left: 5px !important;
    padding-right: 5px !important;
    align-items: center !important;

    p{
      line-height: 30px;
      margin-bottom: 0 !important;
    }

    button{
      margin-left: auto !important;
      margin-bottom: 0 !important;
      position: relative !important;
      border: 1px solid var(--color-darkGrey) !important;
      background-color: transparent !important;
      padding: 0 4px !important;

      &:before{
        width: 20px;
        height: 20px;
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;
        background-image: url("../icons/icon_close.svg") !important;
        content: '';
      }
    }
  }

  .postbox{
    border-radius: 8px;
    box-shadow: 0 10px 22px 0 rgba(0, 4, 32, .05) !important;
    border: 0 none;
  }
}

.cbxchota-setting_input_file_wrap {
  display: flex;
  width: 100% !important;
  max-width: 500px;
  .cbxchota-setting_marker_preview {
    margin-left: 5px;
    width: 30px;
    height: 30px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    border: 2px dotted #333;
    margin-right: 10px;
    border-radius: 5px;
  }
  .cbxchota-setting_filepicker_btn {
    //margin-left: 5px;
    //background-color: var(--color-primary);
    //color: #fff;

    &:hover {
      background-color: var(--color-primary);
      color: #fff;
    }

    &.cbxchota-setting_filepicked {
      border-top-right-radius: 0 !important;
      border-end-end-radius: 0 !important;
    }
    &.cbxchota-setting_left_space {
      margin-left: 5px;
    }
  }
  .cbxchota-setting_marker {
    position: relative;
    width: 30px;
    height: 30px;
    margin-left: 5px;
    margin-right: 5px;
    border-radius: 5px;
    background-size: cover;
    background-position: center;
    background-color: transparent;

  }
  .cbxchota-setting_marker_hide,
  .cbxchota-setting_trash_hide {
    display: none !important;
  }
  .cbxchota-setting_trash {
    font-size: 30px;
    cursor: pointer;
    color: #FFFFFF;
    background: #ff000078;
    border: 1px solid #ff000078;
    height: 30px;
    width: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-top-right-radius: 3px;
    border-end-end-radius: 3px;
    box-sizing: border-box;
    border-left: none;
    &:hover {
      color:#ff000078;
      background: #FFFFFF;
    }
  }
}